































import Vue from 'vue';
import {Component, Inject, Prop} from 'vue-property-decorator';
import FormItem from '@/components/FormItem.vue';
import dayjs from 'dayjs';

@Component({
  components: {FormItem}
})
export default class NumberPad extends Vue {

  @Prop(Number) readonly value!: number;
  @Prop(String) type!: string;
  @Inject() eventBus!: Vue;
  createdAt = '';
  output = '0';
  notes = '';

  created() {
    this.eventBus.$on('update:createdAt', (createdAt: string) => {
      this.createdAt = createdAt;
      console.log(this.createdAt);
    });
  }

  get createdAtText() {
    const inputValue = new Date();
    if (this.createdAt === dayjs(inputValue).format('YYYY-MM-DD')) {
      return '今天';
    } else {
      return this.createdAt;
    }
  }

  ShowBoard() {
    console.log('1');
    this.eventBus.$emit('update:showBoard', true);
  }

  inputContent(event: MouseEvent) {
    const Button = (event.target as HTMLButtonElement);
    const input = Button.textContent!;
    if (this.output.length === 8) {return;}
    if (this.output === '0') {
      if ('0123456789'.indexOf(input) >= 0) {
        this.output = input;
      } else {
        this.output += input;
      }
      return;
    }
    if (this.output.indexOf('.') >= 0 && input === '.') {return;}
    this.output += input;
  }

  remove() {
    if (this.output.length === 1) {
      this.output = '0';
    } else {
      this.output = this.output.slice(0, -1);
    }
  }

  clear() {
    this.output = '0';
  }

  ok() {
    if (!(this.output === '0')) {
      this.eventBus.$emit('update:notes', {notes: this.notes, amount: parseFloat(this.output)});
      this.$emit('submit', parseFloat(this.output));
      this.output = '0';
      this.notes = '';
      this.$store.commit('cancelShowNumberPad');
      this.$store.commit('cancelShowInput');
      if (!(this.$route.path === '/detail')) {
        this.$store.commit('xxx', this.type);
        this.$router.push(`/detail?tab=${this.$store.state.type}`);
      }
      this.eventBus.$emit('update:select', '');
    } else {
      alert('请输入记账金额！');
    }
  }
}
